.PlaylistResults {
    margin-top: 10px;
    width: auto;
    /*min-width: 560px;*/
    height: 395px;
    overflow-y: scroll;
    margin-left: 1%;
    margin-right: 1%;
    padding: .88rem;
    background-color: rgba(1, 12, 63, 0.7);
    box-shadow: 0 4px 2px 2px #000000;
    cursor: pointer;
  }
  
  h2{
    border-bottom: 1px solid #6f6f6f;
    padding-bottom: .88rem;
  }

  ::-webkit-scrollbar {
      width: 0px;
      background: transparent;
  }
  
  @media only screen and (max-width: 1020px) {
    .PlaylistResults {
      width: auto;
      margin-bottom: 2rem;
    }
  }